'use strict';

import Template from './template.html';


export default () => {

	return {

		replace: true,
		restrict: 'EA',
		template: Template,
		controller: ['$scope', 'Popup', ($scope, Popup) => {

			$scope._popup = Popup.data;

			$scope.cancel = () => Popup.closed();
			$scope.confirm = () => Popup.ok();

		}]

	};

}


module.exports.$inject = [];


