'use strict';


export default () => {


	class Popup {

		constructor () {}


		ok () {
			_private.methods.closed();
			_private.confirm && _private.confirm();
		}

		closed () {
			_private.methods.closed();
			_private.cancel && _private.cancel();
		}


		get data () {
			return _private.data;
		}

		set template ( template ) {
			_private.data.template = template;
			_private.data.open = true;
		}

		set content ( content ) {
			_private.data.content = content;
			_private.data.open = true;
		}

		set title (title) {
			_private.data.title = title;
		}

		set cancel ( fn ) {
			if( typeof fn === 'function' )
				_private.cancel = fn;
		}

		set confirm ( fn ) {
			if( typeof fn === 'function' )
				_private.confirm = fn;
		}

	}

	var _private = {

		data : {
			open : false,
			title: null,
			template: null,
			content: null
		},

		cancel : null,
		confirm: null,

		methods : {
			closed : () => {
				_private.data.open = false;
				_private.data.content = null;
				_private.data.template = null;
				_private.data.title = null;
			}
		}

	};

	return new Popup;


}


